        <div class="userinfo-wrap">
          <UserInfo :user-id="userId" />
        </div>
        <div class="tabs-wrap">
          <el-tabs v-model="activeName" class="ant-tabs">
            <el-tab-pane label="发布" name="posts">
              <div class="pane-content" v-if="activeName === 'posts'">
                <UserPosts :userId="userId" />
              </div>
            </el-tab-pane>
            <el-tab-pane label="收藏" name="favorite">
              <div class="pane-content" v-if="activeName === 'favorite'">
                <MoviesFavorite :userId="userId" />
              </div>
            </el-tab-pane>
            <el-tab-pane v-if="self" label="记录" name="history">
              <div class="pane-content" v-if="activeName === 'history'">
                <MoviesHistory :userId="userId" />
              </div>
            </el-tab-pane>
            <!-- <el-tab-pane v-if="self" label="消息列表" name="chat">
              <div class="pane-content" v-if="activeName === 'chat'">
