<script lang="ts">
import { ref, watch, toRefs } from 'vue';
import { useRouter } from 'vue-router';
import UserInfo from '@/views/parts/UserInfo.vue';
import UserPosts from '@/views/parts/UserPosts.vue';
import MoviesFavorite from '@/views/parts/MoviesFavorite.vue';
import MoviesHistory from '@/views/parts/MoviesHistory.vue';
import Chats from './parts/Chats.vue';
import Notifications from './parts/Notifications.vue';
import PublicNotifications from './parts/PublicNotifications.vue';

const Tabs = {
  posts: 'posts',
  favorite: 'favorite',
  history: 'history',
  chat: 'chat',
  comment: 'comment',
  like: 'like',
  notice: 'notice'
};

export default {
  components: {
    UserInfo,
    UserPosts,
    MoviesFavorite,
    MoviesHistory,
    Chats,
    Notifications,
    PublicNotifications
  },
  props: ['params', 'query'],
  setup(props) {
    const { params, query } = toRefs(props);
    const userId = ref(Number(params.value?.id));
    const activeName = ref(Tabs[String(query.value?.tab)] || Tabs.posts);
    const router = useRouter();

    watch(query, (value, prevValue) => {
      activeName.value = Tabs[String(value?.tab)] || Tabs.posts;
    });

    watch(activeName, (value, prevValue) => {
      router.replace({ query: { tab: activeName.value } });
    });

    return {
      userId,
      activeName
    };
  },
  computed: {
    me() {
      return this.$store.state.user.user;
    },
    self() {
      return this.userId === this.me?.id;
    }
  }
};
</script>

