<template>
  <div>
    <Skeleton v-if="loading" type="comments" />
    <div v-else-if="chats?.length">
      <div class="title">最近消息</div>
      <ul class="chat-list" v-infinite-scroll="loadMore" :infinite-scroll-disabled="!hasMorePages">
        <li v-for="chat in chats" :key="chat?.id" class="flex-row chat-item">
          <a :href="'/user/' + chat?.withUser?.id" class="avatar">
            <img v-lazyload="chat?.withUser?.avatar" alt="avatar" class="user-avatar" />
          </a>
          <a href="javascript:void(0)" @click.prevent="toChatPage(chat)" class="chat-info">
            <div class="flex-row">
              <div class="user-name text-ellipsis">
                {{ chat?.subject || chat?.withUser?.name }}
              </div>
              <div class="flex-row">
                <span v-if="chat?.unreads" class="badge">{{ unreads(chat?.unreads || 0) }}</span>
                <span class="meta"> {{ chat?.time_ago }}</span>
              </div>
            </div>
            <p class="last-message text-ellipsis" v-html="lastMessage(chat)"></p>
          </a>
        </li>
      </ul>
    </div>
    <Empty v-else type="content" description="暂无内容" />
    <LoadingSpinner v-if="loadingMore" />
  </div>
</template>

<script lang="ts">
import { ref } from 'vue';
import { useStore } from 'vuex';
import { ElMessage } from 'element-plus';
import { parsingEmoji } from 'vemoji-list';
import { useChatsQuery } from '@/graphql';
import moment from '@/utils/moment';
import Skeleton from '@/components/Skeleton.vue';
import Empty from '@/components/Empty.vue';
import LoadingSpinner from '@/components/LoadingSpinner.vue';

export default {
  name: 'Chats',
  components: {
    Skeleton,
    Empty,
    LoadingSpinner
  },
  setup() {
    const store = useStore();
    const user_id = ref(store.state?.user?.user?.id);

    const { chats, loading, loadMore, loadingMore, hasMorePages } = useChatsQuery(
      { user_id: user_id.value },
      { enabled: !!user_id.value }
    );

    const toChatPage = chat => {
      if (chat?.withUser?.id) {
        window.location.href = `/chat?chat_id=${chat?.id}`;
      } else {
        ElMessage.error('聊天对象丢失!');
      }
    };

    return {
      chats,
      loading,
      loadMore,
      loadingMore,
      hasMorePages,
      toChatPage
    };
  },
  methods: {
    unreads(num) {
      return moment.transNumber(num);
    },
    lastMessage(chat) {
      return parsingEmoji(chat?.lastMessage?.message || (chat?.last_message_id ? '' : '“还没有发过消息”'));
    }
  }
};
</script>

