
import { ref, watch, computed, toRefs } from 'vue';
import { useQuery, useResult } from '@vue/apollo-composable';
import { mergeProperty } from '@/utils';
import { LIKE_NOTIFICATIONS_QUERY, COMMENT_NOTIFICATIONS_QUERY } from '@/graphql';
import Skeleton from '@/components/Skeleton.vue';
import Empty from '@/components/Empty.vue';
import LoadingSpinner from '@/components/LoadingSpinner.vue';

export default {
  props: {
    type: String
  },
  components: {
    Skeleton,
    Empty,
    LoadingSpinner
  },
  setup(props: { type: 'like' | 'comment' }) {
    const { type } = toRefs(props);
    const isQueryLiked = ref(type.value === 'like'); // 是否查询点赞通知

    const { result, loading, fetchMore } = useQuery(
      isQueryLiked ? LIKE_NOTIFICATIONS_QUERY : COMMENT_NOTIFICATIONS_QUERY,
      { page: 1 }
    );
    const notifications = useResult(result, [], (data: any) => data?.notifications?.data);
    const paginatorInfo = useResult(result, {}, (data: any) => data?.notifications?.paginatorInfo);

    const currentPage = ref(paginatorInfo.value?.currentPage);
    const hasMorePages = ref(paginatorInfo.value?.hasMorePages);
    watch(paginatorInfo, (value, prevValue) => {
      currentPage.value = value?.currentPage;
      hasMorePages.value = value?.hasMorePages;
    });
    const loadingMore = ref(false);
    const disabled = computed(() => loading.value || loadingMore.value || !hasMorePages.value);

    function loadMore() {
      loadingMore.value = true;
      fetchMore({
        variables: {
          page: currentPage.value + 1
        },
        updateQuery: (prev: any, { fetchMoreResult }) => {
          loadingMore.value = false;
          if (!fetchMoreResult) {
            return prev;
          }
          return mergeProperty(prev, fetchMoreResult);
        }
      });
    }

    return {
      isQueryLiked,
      loading,
      notifications,
      loadMore,
      disabled,
      loadingMore,
      hasMorePages
    };
  }
};
