<template>
  <div>
    <div v-if="loading" class="placeholder">
      <div v-for="item in 4" :key="item">
        <Skeleton type="media" />
      </div>
    </div>
    <div v-else-if="publicNotices?.length">
      <div class="title">系统通知</div>
      <ul class="notice-list" v-infinite-scroll="loadMore" :infinite-scroll-disabled="disabled">
        <li v-for="notice in publicNotices" :key="notice?.id" class="notice-item">
          <img :src="notice?.user?.avatar" alt="cover" class="notice-cover" />
          <div class="notice-info">
            <p class="notice-title">{{ notice?.title }}</p>
            <p class="notice-desc">{{ notice?.content }}</p>
            <p class="expires-time">{{ notice?.expires_at }}过期</p>
          </div>
        </li>
      </ul>
    </div>
    <Empty v-else type="content" description="暂无内容" />
    <LoadingSpinner v-if="loadingMore" />
  </div>
</template>

<script lang="ts">
import { ref, watch, computed } from 'vue';
import { useQuery, useResult } from '@vue/apollo-composable';
import { mergeProperty } from '@/utils';
import { PUBLIC_NOTIFICATIONS_QUERY } from '@/graphql';
import Skeleton from '@/components/Skeleton.vue';
import Empty from '@/components/Empty.vue';
import LoadingSpinner from '@/components/LoadingSpinner.vue';

export default {
  components: {
    Skeleton,
    Empty,
    LoadingSpinner
  },
  setup() {
    const { result, loading, error, fetchMore } = useQuery(PUBLIC_NOTIFICATIONS_QUERY, { page: 1 });
    const publicNotices = useResult(result, [], (data: any) => data?.notices?.data);
    const paginatorInfo = useResult(result, {}, (data: any) => data?.notices?.paginatorInfo);

    const currentPage = ref(paginatorInfo.value?.currentPage);
    const hasMorePages = ref(paginatorInfo.value?.hasMorePages);
    watch(paginatorInfo, (value, prevValue) => {
      currentPage.value = value?.currentPage;
      hasMorePages.value = value?.hasMorePages;
    });
    const loadingMore = ref(false);
    const disabled = computed(() => loading.value || loadingMore.value || !hasMorePages.value);

    function loadMore() {
      loadingMore.value = true;
      fetchMore({
        variables: {
          page: currentPage.value + 1
        },
        updateQuery: (prev: any, { fetchMoreResult }) => {
          loadingMore.value = false;
          if (!fetchMoreResult) {
            return prev;
          }
          return mergeProperty(prev, fetchMoreResult);
        }
      });
    }
    return {
      publicNotices,
      loading,
      loadMore,
      disabled,
      loadingMore,
      hasMorePages
    };
  }
};
</script>

