
import { ref } from 'vue';
import { useStore } from 'vuex';
import { ElMessage } from 'element-plus';
import { parsingEmoji } from 'vemoji-list';
import { useChatsQuery } from '@/graphql';
import moment from '@/utils/moment';
import Skeleton from '@/components/Skeleton.vue';
import Empty from '@/components/Empty.vue';
import LoadingSpinner from '@/components/LoadingSpinner.vue';

export default {
  name: 'Chats',
  components: {
    Skeleton,
    Empty,
    LoadingSpinner
  },
  setup() {
    const store = useStore();
    const user_id = ref(store.state?.user?.user?.id);

    const { chats, loading, loadMore, loadingMore, hasMorePages } = useChatsQuery(
      { user_id: user_id.value },
      { enabled: !!user_id.value }
    );

    const toChatPage = chat => {
      if (chat?.withUser?.id) {
        window.location.href = `/chat?chat_id=${chat?.id}`;
      } else {
        ElMessage.error('聊天对象丢失!');
      }
    };

    return {
      chats,
      loading,
      loadMore,
      loadingMore,
      hasMorePages,
      toChatPage
    };
  },
  methods: {
    unreads(num) {
      return moment.transNumber(num);
    },
    lastMessage(chat) {
      return parsingEmoji(chat?.lastMessage?.message || (chat?.last_message_id ? '' : '“还没有发过消息”'));
    }
  }
};
